const formatData = data => {
  return data.map(item => {
    const { id, lunchContent, orderStart, orderEnd, remarks, color } = item
    return {
      id,
      name: lunchContent,
      orderStart,
      orderEnd,
      remarks,
      color
    }
  })
}

const pickMenus = menus => menus.split(' ')

const pickMyOrders = orders => {
  const ordersObj = {}
  orders.forEach(item => {
    ordersObj[item.day] = {}

    if (item.lunchType === '1') {
      ordersObj[item.day].lunch = true
    }

    if (item.lunchType === '2') {
      ordersObj[item.day].dinner = true
    }
  })
  return ordersObj
}

export { formatData, pickMenus, pickMyOrders }
